body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
	margin: 0 auto;
	position: fixed;
	top: 0;
	z-index: 1;
	height: 3em;
	width: 100%;
	background-color: #202322;
	color: black;

	display: flex;
	flex-direction: row;
}

.navbar .navbar-left {
	left: 0;
	width: 25%;
	display: flex;
	margin-left: 1%;
	align-items: center;
}

.navbar .navbar-center {
	margin-left: 0;
	margin-right: 0;
	width: 83%;
	display: flex;
	flex-direction: row;
	align-items: center;
	/*justify-content: space-between;*/
}

.navbar-title {
	color: #61dafb;
	font-weight: 300;
	font-size: 1.8em;
	/*background-color: blue;*/
}

.navbar-button {
	color: white;
	margin-right: 2em;
	font-weight: 300;
	font-size: 1.8em;
	/*background-color: red;*/
}

@media (max-width: 50em) {
	.navbar .navbar-left {
		left: 0;
		width: 0%;
		align-items: center;
	}

	.navbar .navbar-center{
		width: 100%;
		justify-content: space-between;
		margin-right: 10%;
		margin-left: 10%;
	}
	.navbar-title {
		font-size: 0em;
	}

	.navbar-button {
		font-size: 1.6em;
		margin-right: 0em;
	}

  .navbar-button {
		/*font-size: 1.4em; */
  }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 20em;
}

.App-header {
  /*background-color: #282c34;*/
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

