.main {
	width: 55em;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-top: 2em;
}

.project-container1 {
	height: 36em;
	width: 52em;
	border-radius: .4em;
	border-color: black;
	padding: 1em;
	margin-bottom: 1em;
	background-image: -webkit-linear-gradient(right,#5a40d8,#d84add);
	background-image: linear-gradient(-90deg,#5a40d8,#d84add);

	position: relative;
}

.project-container2  {
	height: 40em;
	width: 52em;
	border-radius: .4em;
	border-color: black;
	padding: 1em;
	margin-bottom: 1em;
	background-image: -webkit-linear-gradient(right,#4064d8,#4ad8dd);
	background-image: linear-gradient(-90deg,#4064d8,#4ad8dd);

	position: relative;
}

.project-container3  {
	height: 40em;
	width: 52em;
	border-radius: .4em;
	border-color: black;
	padding: 1em;
	margin-bottom: 1em;
	background-image: -webkit-linear-gradient(right,#0da382,#67e44e);
	background-image: linear-gradient(-90deg,#0da382,#67e44e);

	position: relative;
}

.project-image {
	position: absolute;
	left: 1em;
	top: 1em;
	bottom: 1em;
	width: 25.5em;
	background-color: black;

	display: flex;
	/*align-items: center;*/
	justify-content: center;
}

.project-logo {
	margin-top: 10%;
	width: 90%;
	height: 70%;
}

.project-container1:hover .project-image .printer-animation{
	opacity: 1;
}

.project-container2:hover .project-image .propulsion-animation{
	-webkit-animation: flicker infinite .2s linear;
	        animation: flicker infinite .2s linear;
}

@-webkit-keyframes flicker {
  from {
    opacity: .2;
  }
  to {
    opacity: 1;
  }
}

@keyframes flicker {
  from {
    opacity: .2;
  }
  to {
    opacity: 1;
  }
}

.project-container3:hover .project-image .project-logo {
	/*display: none;*/
}
.project-container3:hover .project-image .cherishly-animation{
	opacity: 1;
}

.printer-animation {
	position: absolute;
	opacity: 0;
	transition: opacity 1s ease-out;
    -moz-transition: opacity 1s ease-out;
    -webkit-transition: opacity 1s ease-out;
    -o-transition: opacity 1s ease-out;
	bottom: 1em;
	left: .5em;
	width: 100%;
	height: 6em;
}

.propulsion-animation {
	position: absolute;
	opacity: 0;
	bottom: 2em;
	width: 51%;
	height: 6em;
}

.ion-thruster-logo {
	width: 100%;
}

.cherishly-logo {
	margin-top: 10%;
	width: 86%;
	height: 86%;
}

.cherishly-animation {
	margin-top: 10%;
	position: absolute;
	top: 0;
	width: 86%;
	height: 86%;

	opacity: 0;
	transition: opacity 1s ease-out;
    -moz-transition: opacity 1s ease-out;
    -webkit-transition: opacity 1s ease-out;
    -o-transition: opacity 1s ease-out;
}

.project-title {
	position: absolute;
	right: 1em;
	top: 1em;
	height: 7em; 
	width: 24.5em;
	background-color: black;
}

.project-title h1 {
	text-align: left;
	padding-left: 1em;
	color: white;
	font-size: 2.7em;
}

.project-description {
	position: absolute;
	right: 1em;
	bottom: 1em;
	height: 74%; 
	width: 24.5em;
	background-color: black;

	color: white;
}

.project-description p {
	text-align: left;
	padding-left: 1em;
	padding-right: 1em;
	color: white;
	font-size: 2.25em;
	font-weight: 1px;
	/*font-family: "helvetica-neue";*/
}

@media (max-width: 54em) {
	.navbar-title {
		font-size: 0em;
	}

	.project-container1 {
		width: 20em;
		height: 34em;
	}
	.project-container2 {
		width: 20em;
		height: 34em;
	}
	.project-container3 {
		width: 20em;
		height: 34em;
	}
	.project-image {
		width: 20em;
		height: 30em;
	}
	.project-title {
		position: absolute;
		top: 30em;
		height: 5em; 
		width: 20em;
		background-color: black;
	}
	.project-title h1 {
		text-align: center;
		padding-right: 1em;
		color: white;
		font-size: 2.3em;
	}
	.project-description {
		display: none;
	}
	.printer-animation {
		width: 95%;
		bottom: 0em;
		left: .5em;
		height: 6em;
	}
	.propulsion-animation {
		position: absolute;
		opacity: 0;
		bottom: .5em;
		width: 51%;
		height: 6em;
	}
}
